import React, { Component } from "react";
import {
    VerticalTimeline,
    VerticalTimelineElement,
} from "react-vertical-timeline-component";
import "react-vertical-timeline-component/style.min.css";
import Badge from "react-bootstrap/Badge";

class Studies extends Component {
    render() {
        if (this.props.resumeStudies && this.props.resumeBasicInfo) {
            var sectionName = this.props.resumeBasicInfo.section_name.studies;
            var work = this.props.resumeStudies.map(function (work, i) {
                const technologies = work.technologies;
                const mainTechnologies = work.mainTech;

                var mainTech = mainTechnologies.map((technology, i) => {
                    return (
                        <Badge pill className="main-badge mr-2 mb-2" key={i}>
                            {technology}
                        </Badge>
                    );
                });
                var tech = technologies.map((technology, i) => {
                    return (
                        <Badge pill className="studies-badge mr-2 mb-2" key={i}>
                            {technology}
                        </Badge>
                    );
                });
                return (
                    <VerticalTimelineElement
                        className="vertical-timeline-element--work"
                        date={work.years}
                        iconStyle={{
                            background: "#AE944F",
                            color: "#fff",
                            textAlign: "center",
                        }}
                        icon={<i className="fab fa-angular study-icon"></i>}
                        key={i}
                    >
                        <div style={{ textAlign: "left", marginBottom: "4px" }}>
                            {mainTech}
                        </div>

                        <h3
                            className="vertical-timeline-element-title"
                            style={{ textAlign: "left" }}
                        >
                            {work.title}
                        </h3>
                        <h4
                            className="vertical-timeline-element-subtitle"
                            style={{ textAlign: "left" }}
                        >
                            {work.company}
                        </h4>
                        <div style={{ textAlign: "left", marginTop: "15px" }}>{tech}</div>
                    </VerticalTimelineElement>
                );
            });
        }

        return (
            <section id="studies" className="pb-5">
                <div className="col-md-12 mx-auto">
                    <div className="col-md-12">
                        <h1 className="section-title" style={{ color: "white" }}>
              <span className="text-black" style={{ textAlign: "center" }}>
                {sectionName}
              </span>
                        </h1>
                    </div>
                </div>
                <div className="col-md-8 mx-auto">
                    <VerticalTimeline>
                        {work}
                        <VerticalTimelineElement
                            iconStyle={{
                                background: "#AE944F",
                                color: "#fff",
                                textAlign: "center",
                            }}
                            icon={
                                <i className="fas fa-hourglass-start mx-auto study-icon"></i>
                            }
                        />
                    </VerticalTimeline>
                </div>
            </section>
        );
    }
}

export default Studies;
