import React, { Component } from "react";

class CV extends Component {
    render() {
        let sectionName = "";
        let cvIcons = [];
        let cvDescription = "";

        if (this.props.resumeBasicInfo) {
            sectionName = this.props.resumeBasicInfo.section_name.cv;
        }

        if (this.props.resumeCV && this.props.resumeBasicInfo) {
            var cv = this.props.resumeCV.icons.map((cv, i) => (
                <li className="list-inline-item mx-3" key={i}>
          <span>
            <div className="text-center skills-tile">
              <i className={cv.class} style={{ fontSize: "220%" }}>
                <p className="text-center" style={{ fontSize: "30%", marginTop: "4px" }}>
                  {cv.name}
                </p>
              </i>
            </div>
          </span>
                </li>
            ));
        }

        let pdfUrl = "";
        if (this.props.cvData && this.props.cvData.CV && this.props.cvData.CV.length > 0) {
            pdfUrl = this.props.cvData.CV[0].dir; // Obtiene la direcci√≥n del primer objeto CV en el arreglo
        }

        if (this.props.resumeBasicInfo && this.props.resumeBasicInfo.description_cv) {
            cvDescription = this.props.resumeBasicInfo.description_cv;
        }

        return (
            <section id="cv">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-12 text-center">
                            <h2 className="section-heading text-uppercase">{sectionName}</h2>
                            <ul className="list-inline">{cvIcons}</ul>
                            <div className="mt-4">
                                <p className="section-subheading text-muted">{cvDescription}</p>
                                <a href={pdfUrl} className="btn btn-primary btn-xl" download>
                                    {cv.instruction}
                                </a>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}

export default CV;
